.movie-info {
    padding-top: var(--sectionPadding);
    padding-bottom: var(--sectionPadding);
    display: flex;
    align-items: center;
}

.movie-info .container {
    display: flex;
    justify-content: space-between;
    border: 1px solid var(--borderColor);
    border-radius: var(--BigBorderRadius);
    box-shadow: -4px 4px 0 0 var(--secondColor);
    padding-top: 20px;
    padding-bottom: 20px;
}

.movie-info .container .imgSection {
    width: 30%;
}

.movie-info .container .imgSection img {
    height: 350px;
}

.movie-info .container .imgSection img,
.movie-info .container .imgSection button {
    width: 100%;
    border-radius: var(--smallBorderRadius);
    margin-bottom: 5px;
}

.movie-info .container .txtSection {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.txtSection .resolution {
    display: flex;
    justify-content: space-between;
}

.txtSection .resolution a {
    width: calc(60% / 4);
    text-decoration: none;
    padding: 10px;
    border: 1px solid var(--mainColor);
    border-radius: var(--smallBorderRadius);
    color: var(--secondColor);
    text-align: center;
}

.subtitles a {
    display: block;
    text-align: center;
    text-decoration: none;
    padding: 10px;
    border: 1px solid var(--mainColor);
    border-radius: var(--smallBorderRadius);
    color: var(--secondColor);
}

@media (max-width: 800px) {
    .movie-info .container {
        width: 80%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .movie-info .container .imgSection {
        width: 80%;
        margin-bottom: 25px;
    }

    .movie-info .container .txtSection {
        width: 80%;
        align-items: center;
    }

    .movie-info .container .txtSection > * {
        margin-bottom: 10px;
    }

    .txtSection div {
        width: 100%;
    }
}

@media (max-width: 400px) {
    .txtSection .resolution {
        flex-direction: column;
    }

    .txtSection .resolution a {
        width: auto;
        margin-bottom: 10px;
    }
}